import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import './navbar.css';

const Navbar = () => {
    const [menu, setMenu] = useState(null);
    const [menuOpen, setMenuOpen] = useState(false);

    const handleClick = (category) => {
        setMenu(category);
        setMenuOpen(false); // Close the menu when a link is clicked
    };

    const toggleMenu = () => {
        setMenuOpen(!menuOpen);
    };

    return (
        <div className='navbar_container'>
            <div className={`navbar ${menuOpen ? 'active' : ''}`}>
                <div className='logo'>
                    <Link style={menu === "trang_chu" ? { borderBottom: "none" } : {}} onClick={() => handleClick('trang_chu')} to={'/'}>
                        <img src={"https://movix-taupe.vercel.app/assets/movix-logo-d720c325.svg"} alt="logo" />
                    </Link>
                </div>
                <button className='menu-toggle' onClick={toggleMenu}>
                    {menuOpen ? '' : ''}
                </button>
                <div className={`bar ${menuOpen ? 'active' : ''}`}>
                    <ul>
                        <li><Link style={menu === "trang_chu" ? { borderBottom: "none" } : {}} onClick={() => handleClick('trang_chu')} to='/'>Trang Ch</Link></li>
                        <li><Link style={menu === "phim_moi_cap_nhat" ? { borderBottom: "3px solid rgb(139 92 246 )" } : {}} onClick={() => handleClick('phim_moi_cap_nhat')} to='/movie/phim_moi_cap_nhat'>Tt C Phim</Link></li>
                        <li><Link style={menu === "phim_le" ? { borderBottom: "3px solid rgb(139 92 246 )" } : {}} onClick={() => handleClick('phim_le')} to='/movie/phim_le'>Phim L</Link></li>
                        <li><Link style={menu === "phim_bo" ? { borderBottom: "3px solid rgb(139 92 246 )" } : {}} onClick={() => handleClick('phim_bo')} to='/movie/phim_bo'>Phim B</Link></li>
                        <li><Link style={menu === "tvshow" ? { borderBottom: "3px solid rgb(139 92 246 )" } : {}} onClick={() => handleClick('tvshow')} to='/movie/tvshow'>Tv Show</Link></li>
                        <li><Link style={menu === "phim_hoat_hinh" ? { borderBottom: "3px solid rgb(139 92 246 )" } : {}} onClick={() => handleClick('phim_hoat_hinh')} to='/movie/phim_hoat_hinh'>Phim Hot Hnh</Link></li>
                    </ul>
                </div>
            </div>
        </div>
    );
};

export default Navbar;
